import React from 'react';
import styled from 'styled-components';
import { themeVal, glsp } from '@theme/utils';
import { media } from '@theme/media';

import { FoldContent, FoldInner } from '../styles/fold';
import { Heading, renderHeadingSize } from '../styles/heading';
import { Lead } from '../styles/type';
import { Button } from '../styles/button';
import { IconDiscord } from './icons/discord';

import communitySceneUrl from '../images/community-scene.jpg';

const CommunityFold = styled.div`
  background-color: ${themeVal('color.neut1')};
  color: ${themeVal('color.neut3')};
  background-image: url(${communitySceneUrl});
  background-size: cover;

  ${Heading} {
    ${media.smallDown`
      ${renderHeadingSize({ size: 'large' })}
    `}
  }

  ${FoldInner} {
    padding-top: ${glsp(3)};
    padding-bottom: ${glsp(3)};

    ${media.largeUp`
      padding-top: ${glsp(5)};
      padding-bottom: ${glsp(4)};
    `}
  }

  ${FoldContent} {
    grid-column: auto / span 4;

    ${media.mediumUp`
      grid-column: auto / span 4;
    `}

    ${media.largeUp`
      grid-column: auto / span 6;
    `}
  }

  ${Button} {
    margin-top: ${glsp(2)};
    gap: ${glsp()};
  }
`;

export default function CommunitySection() {
  return (
    <section>
      <CommunityFold>
        <FoldInner>
          <FoldContent>
            <Heading as='h2' size='xlarge' variation='surface'>
              Community
            </Heading>
            <Lead>
              The Thug Pugs Community is a vibrant space where we and our
              holders participate in governance votes, engage with ideas,
              express our creativity and simply have fun.
            </Lead>
            <Button
              size='large'
              variation='primary-fill'
              as='a'
              href='https://discord.gg/thugpugs'
            >
              <IconDiscord /> Join our Discord
            </Button>
          </FoldContent>
        </FoldInner>
      </CommunityFold>
    </section>
  );
}
